.product-container {
  --columns-count: 1;
  padding: 50px 40px;

  display: grid;
  grid-auto-flow: row;

  grid-template-columns: repeat(var(--columns-count), $product-card-width);
  grid-template-rows: repeat(2, $product-card-height);

  gap: 46px 48px;

  justify-content: space-evenly;
  align-content: space-between;

  @media (min-width: 488px) {
    & {
      --columns-count: 2;
    }
  }

  @media (min-width: 768px) {
    & {
      --columns-count: 3;
    }
  }

  @media (min-width: 1024px) {
    & {
      --columns-count: 4;
    }
  }
}
